@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');


$fundo--padrao: rgb(250,250,250);
$fundo--janela: rgb(180,180,180);
$fundo--title--bar: rgb(230, 230, 230);
$fundo--menu--lateral--padrao: #fafafa;
$fundo--menu--superior--padrao: $fundo--menu--lateral--padrao;
$fundo--menu--opcoes: #a7a7a7;
$botao--hover: #cccccc;
$borda--padrao: #000000;

$sombra--padrao: 1px 1px 5px 1px rgba(0, 0, 0, 0.3);

$font--roboto: 'Roboto';
$font--color--clara: white;

$crddrs: linear-gradient(118deg, rgba(0,167,30,1) 10%, rgba(255,0,0,1) 50%, rgba(255,252,0,1) 90%);
    
$large: 1200px;
$midium: 800px;
$small: 799px;

@page 
    {
        size: auto;   /* auto is the initial value */
        margin: 0mm;  /* this affects the margin in the printer settings */
    }
    
html {
    font-family: $font--roboto;
}

body {
    background: $fundo--janela;
    scroll-behavior: smooth;
    &::-webkit-scrollbar {
        width: 0px;
    }
}
