

.botoes{
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    button {
        border: 0px;
        background-color: rgba($color: #000000, $alpha: 0);
        padding: 0;
        &:hover {
            cursor: pointer;
        }
    }
}

// .inativo {
//     color: red;
// }

.inativo {
    background-color: rgba($color: red, $alpha: 0.2);
    &:hover{
        background-color: rgba($color: red, $alpha: 0.3);
    }
}
.ativo {
    &:hover{
        background-color: rgba($color: silver, $alpha: 0.4);
    }
}
