@import '../../scss/__variaveis';

@media print {
    .info {
        display: none;
    }
    .lateral {
        display: none;
    }
}

@media screen and (min-width: $large) {
    .containerFundo {
        min-height: 70vh;
        max-height: max-content;
        background-color: $fundo--janela;
        padding-bottom: 20px;

        .info {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 100vw;
            background: $fundo--padrao;
            border-bottom: 1px solid black;

            p {
                padding: 8px 20px;
                font-size: 16pt;
            }
        }

        .container {
            width: 100%;
            min-height: 94vh;
            display: flex;
            align-items: flex-start;

            .lateral {
                width: 250px;
                position: sticky;
                position: -webkit-sticky;
                top: 0;
                margin: 0px;
                background: $fundo--padrao;

                p {
                    font-size: large;
                    font-weight: 800;
                }
            }

            .boxCentral {
                width: 80%;
                height: max-content;
                margin: 10px 5% 10px 20px;
                padding: 20px;
                border: 1px solid black;
                border-radius: 15px;
                background-color: rgba($color: #dedede, $alpha: 1.0);
            }
        }
    }
}


