@import '../../../scss/_variaveis';

@media screen and (min-width: $midium) {
    .login {
        width: 230px;
        height: fit-content;
        border-radius: 5px;
        box-shadow: $sombra--padrao;
        border-top: 30px solid #2e7d32;
        background-color: white;
        h1 {
            position: relative;
            text-align: center;
            font-size: 16pt;
            bottom: 25px;
            color: white;
        }
        .login--form {
            position: relative;
            bottom: 20px;
        }
        .login-user {
            width: 90%;
            height: auto;
            margin: 15px auto;
            button {
                margin-bottom: 10px;
            }
        }
        p {
            font-size: 9pt;
            text-align: center;
            margin: auto;
            &:hover {
                cursor: pointer;
                text-decoration: underline;
            }
        }
    }
    .box--container {
        width: fit-content;
        height: fit-content;
        background-color: white;
        margin: 0 100px 50px 20px;
        padding: 10px 20px 50px 10px;
        h1 {
            font-size: 12pt;
            font-weight: 600;
        }
        div {
            display: flex;
            flex-wrap: wrap;
            div {
                display: flex;
                flex-direction: column;
                padding: 10px;
                width: 270px;
                h2 {
                    font-size: 10pt;
                    font-weight: 600;
                    padding: 5px;
                }
                li {
                    font-size: 8pt;
                    padding: 2px;
                }
            }
        }
    }
}
@media screen and (max-width: $small) {
    .login {
        width: 100%;
        height: fit-content;
        box-shadow: $sombra--padrao;
        border-top: 30px solid #2e7d32;
        margin-top: 20px;
        background-color: white;
        h1 {
            position: relative;
            text-align: center;
            font-size: 16pt;
            bottom: 25px;
            color: white;
        }
        #login--form {
            position: relative;
            bottom: 20px;
        }
        .login-user {
            width: 90%;
            height: auto;
            margin: 15px auto;
        }
        .botao {
            margin-bottom: 10px;
        }
            
        p {
            font-size: 9pt;
            text-align: center;
            margin: auto;
            &:hover {
                cursor: pointer;
                text-decoration: underline;
            }
        }
    }
    .box--container {
        width: fit-content;
        height: fit-content;
        background-color: white;
        margin: 20px auto 50px auto;
        padding: 10px 10px 50px 10px;
        h1{
            text-align: center;
            font-size: 18pt;
            font-weight: 600;
        }
        div {
            display: flex;
            flex-direction: column;
            align-items: center;
            div {
                display: flex;
                flex-direction: column;
                padding: 10px;
                width: fit-content;
                margin: auto;
                h2 {
                    font-size: 16pt;
                    font-weight: 600;
                    padding: 5px;
                }
                li {
                    font-size: 14pt;
                    padding: 2px;
                }
            }
        }
    }
}
