@import '../../scss/_variaveis';

@media print {
    .title--bar {
        display: none;
    }

    .faixa--bar {
        display: none;
    }

    .space {
        display: none;
    }
}


@media screen and (min-width: $midium) {
    .title--bar {
        display: flex;
        height: 150px;
        flex-direction: row;
        border-bottom: 2px solid black;
        background-color: $fundo--title--bar;

        img {
            width: 170px;
            margin-left: 10%;
            border-bottom: 0px;
        }

        h1 {
            margin: auto auto auto 20px;
            font-size: 16pt;
        }
    }

    .faixa--bar {
        height: 30px;
        display: flex;
        background-color: black;

        .preto1 {
            width: 80vw;
            height: 30px;
        }

        .verde {
            width: 50px;
            height: 30px;
            background-color: #2e7d32;
            -webkit-transform: skew(-30deg);
            -ms-transform: skew(-30deg);
            transform: skew(-30deg);
        }

        .vermelho {
            width: 50px;
            height: 30px;
            background-color: red;
            -webkit-transform: skew(-30deg);
            -ms-transform: skew(-30deg);
            transform: skew(-30deg);
        }

        .amarelo {
            width: 15vw;
            height: 30px;
            background-color: yellow;
            -webkit-transform: skewX(-30deg);
            -ms-transform: skewX(-30deg);
            transform: skewX(-30deg);
        }

        .preto2 {
            height: 30px;
            width: 20px;
        }
    }

    .space {
        height: 30px;
        border-top: 2px solid black;
        border-bottom: 1px solid;
        background: $fundo--padrao;
    }
}

@media screen and (max-width: $small) {
    .title--bar {
        display: flex;
        flex-direction: column;
        background-color: $fundo--title--bar;

        img {
            width: 30%;
            margin: auto;
        }

        h1 {
            margin: 20px;
            font-size: 20pt;
            text-align: center;
        }
    }

    .faixa--bar {
        height: 30px;
        display: flex;
        background-color: black;

        .preto1 {
            width: 80vw;
            height: 30px;
        }

        .verde {
            width: 50px;
            height: 30px;
            background-color: #2e7d32;
            -webkit-transform: skew(-30deg);
            -ms-transform: skew(-30deg);
            transform: skew(-30deg);
        }

        .vermelho {
            width: 50px;
            height: 30px;
            background-color: red;
            -webkit-transform: skew(-30deg);
            -ms-transform: skew(-30deg);
            transform: skew(-30deg);
        }

        .amarelo {
            width: 15vw;
            height: 30px;
            background-color: yellow;
            -webkit-transform: skewX(-30deg);
            -ms-transform: skewX(-30deg);
            transform: skewX(-30deg);
        }

        .preto2 {
            height: 30px;
            width: 20px;
        }
    }

    .space {
        border-top: 2px solid black;
        border-bottom: 2px solid;
    }
}