@import '../../scss/_variaveis';

body {
    background: $fundo--padrao;
    &::-webkit-scrollbar{
        width: 0px;
    }
}

@media screen and (min-width: $midium) {
    .nav--bar {
        width: 100%;
        border-bottom: 1px solid black;
        .nav--item {
            display: flex;
            flex-wrap: nowrap;
            justify-content: center;
            p {
                padding: 5px;
                &:hover {
                    background-color: rgba(200, 200, 200, 0.5);
                }
            }
            &:hover {
                cursor: pointer;
            }
        }
    }
    .box--central {
        display: flex;
        min-height: 70vh;
        margin: 0 auto 0 auto;
        background-color: $fundo--title--bar;
        padding: 20px 150px ;
    }
}
@media screen and (max-width: $small) {
    .nav--bar {
        width: 100%;
        border-bottom: 2px solid black;
        .nav--item {
            display: flex;
            flex-wrap: nowrap;
            justify-content: center;
            p {
                padding: 5px;
                &:hover {
                    background-color: rgba(200, 200, 200, 0.5);
                }
            }
            &:hover {
                cursor: pointer;
            }
        }
    }
    .box--central {
        display: flex;
        flex-direction: column;
        background-color: $fundo--title--bar;
    }
}