.opcoes {
    width: 100%;
    display: flex;

    form {
        padding: 0;
    }
}

h2 {
    white-space: nowrap;
}

.boxlinha {
    display: flex;
    flex-direction: row;
    align-items: center;

    :last-child {
        justify-content: center;
    }
}

.linhaservico {
    padding: 15px 0;
}

.umalinha {
    padding-top: 5px;
}

.container {
    width: 100%;
    display: flex;
    flex-direction: row;
    padding-bottom: 20px;

    .boxleft {
        width: 50%;
        padding: 0 15px 0 15px;
        flex-direction: column;

        .unicoassim {
            display: flex;
            flex-direction: row;

            div:last-child {
                padding-left: 100px;
            }
        }
    }

    .boxrigth {
        width: 50%;
        padding: 0 15px 0 15px;
        display: flex;
        flex-direction: column;
    }
}

.coluna {
    display: flex;
    flex-direction: column;
}

.nocheck {
    padding-left: 30px;
}

.centrador {
    display: flex;
    flex-direction: column;
    align-items: center;

    .data {
        width: 50%;
        padding: 8px 10px 8px 10px;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
    }
}